<template>
  <div>
    <div v-if="cursos">
      <el-form :model="form" :rules="rules" ref="form" label-width="120px">
        <el-form-item label="Curso" prop="cursoStr">
          <el-select v-model="form.cursoStr" placeholder="Selecione o curso">
            <el-option v-for="item in cursos" :label="item" :value="item"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search">OK</el-button>
        </el-form-item>
      </el-form>
      <div v-if="curso">
        <previsao-curso-table :response-data=curso></previsao-curso-table>
      </div>
    </div>

    <div v-if="loading">
      <div class="loader"></div>
    </div>

    <div v-if="error">
      {{ error }}
    </div>
  </div>
</template>

<script>
  import './previsaoCursoTable.js'
  import dataFetching from '../mixins/dataFetching'
  export default {
    name: 'previsao-curso',
    mixins: [dataFetching],
    data() {
      return {
        curso: null,
        cursos: null,
        cursosRef: {
          'Todos os cursos': 0,
          'Bacharelado em Ciências da Computação': 16,
          'Bacharelado em Ciência e Tecnologia': 20,
          'Bacharelado em Ciências Biológicas': 17,
          'Bacharelado em Ciências Econômicas': 1,
          'Bacharelado em Ciências e Humanidades': 22,
          'Bacharelado em Filosofia': 10,
          'Bacharelado em Física': 28,
          'Bacharelado em Matemática': 21,
          'Bacharelado em Neurociência': 24,
          'Bacharelado em Planejamento Territorial': 11,
          'Bacharelado em Políticas Públicas': 31,
          'Bacharelado em Química': 14,
          'Bacharelado em Relações Internacionais': 27,
          'Engenharia Aeroespacial': 26,
          'Engenharia Ambiental e Urbana': 6,
          'Engenharia Biomédica': 18,
          'Engenharia de Energia': 7,
          'Engenharia de Gestão': 23,
          'Engenharia de Informação': 8,
          'Engenharia de Instrumentação, Automação e Robótica': 2,
          'Engenharia de Materiais': 15,
          'Licenciatura em Ciências Biológicas': 13,
          'Licenciatura em Física': 25,
          'Licenciatura em Matemática': 9,
          'Licenciatura em Química': 4
        },
        form: {
          cursoStr: null,
          turno: null
        },
        rules: {
          cursoStr: [
            { required: true, message: 'É necessário escolher um curso', trigger: 'change' }
          ]
        }
      }
    },
    created() {
      this.cursos = Object.keys(this.cursosRef)
    },
    methods: {
      search() {
        this.$refs.form.validate((valid) => {
          if (valid) {
            const id = this.cursosRef[this.form.cursoStr]
            const cb = () => {
              this.curso = this.response.body
              this.response = null
            }
            this.fetchData(`previsao/${id}`, cb)
          } else {
            return false
          }
        })
      }
    }
  }
</script>

<style>
</style>



