import Vue from 'vue'

Vue.component('previsao-curso-table', {
  props: {
    responseData: null,
    turno: null
  },
  data() {
    return {
      rows: [],
      columnKeys: [],
      columnHeaders: [],
      rowHeaders: []
    }
  },
  template: `<div>
    <table>
      <thead>
        <tr>
          <th></th>
          <th v-for="(header, index) in columnHeaders">
            {{ header }}
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(cells, index) in rows">
          <th>{{ rowHeaders[index] }}</th>
          <td v-for="cell in cells">
            {{ cell }}
          </td>
        </tr>
      <tbody>
    <table>
  </div>`,
  methods: {
    sort(index, order) {
      const key = this.columnKeys[index]
      let data = this.responseData
      data = _.orderBy(data, `${this.turno}.${key}`, [order])
      this.rowHeaders = _.map(data, 'nome')
      this.rows = _.map(data, this.turno)
    },
    renderTable() {
      let data = this.responseData
      this.rowHeaders = _.map(data, 'nome')
      this.rows = _.map(data, this.nome)
      this.rows.map(function(item) {
        delete item['nome']
        delete item['curso']
        delete item['codigo']
        delete item['disciplina_id']
        item['vagas-requisicoes'] = item['vagas'] + '/' + item['requisicoes']
        delete item['vagas']
        delete item['requisicoes']
        item['posicao-extensao'] = item['posicao'] + '/' + item['extensao']
        delete item['extensao']
        delete item['posicao']
        item['reserva'] = item.corte.reserva ? 'Sim' : 'Nao'
        item.turno = item.corte.turno
        item.ik = item.corte.ik.toFixed(2)
        item.cp = item.corte.cp
        item.cr = item.corte.cr
        delete item.corte
      })
      this.columnHeaders = Object.keys(data[0])
      const values = Object.values(data)
      this.columnKeys = ['extensao', 'corte']
    }
  },
  created() {
    this.renderTable()
  },
  watch: {
    responseData() {
      this.renderTable()
    }
  }
})



